body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: "BebasNeue";
  src: local("BebasNeue"),
   url("./fonts/Bebas_Neue/BebasNeue-Regular.ttf") format("truetype");
  font-weight: normal;
  }

@font-face {
  font-family: "BlackerSansPro";
  src: local("BlackerSansPro"),
     url("./fonts/BlackerSansPro/BlackerSansPro-Medium.ttf") format("truetype");
    font-weight: bold;
    }

    @font-face {
      font-family: "BlackerSansPro";
      src: local("BlackerSansPro"),
       url("./fonts/BlackerSansPro/BlackerSansPro-Regular.ttf") format("truetype");
      font-weight: normal;
      }
  