.App {
  text-align: center;
  font-family: "BlackerSansPro";
  display: flex;
  flex-direction: column;
  background-color: #1C1B1B;
  display: flex;
  flex-direction: column;
  }

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.navBarContainer {
  display: flex;
  justify-content: flex-end;
  background-color: #262463;
}

.navBarLink {
  width: 100px;
  margin-right: 75px;
}

.bannerContainer {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  padding-top: 100px;
  padding-bottom: 100px;
  background-image: linear-gradient(to right, #000000, #3533CD);
}
.bannerTextContainer {
  display: flex;
  flex-direction: column;
  max-width: 600px;
  justify-content: center;
}

.bannerHeader {
  width: 550px;
}
.bannerSubHeaderText {
  display: flex;
  flex-wrap: wrap;
  color: white;
  font-size: 48px;
  text-align: left;
}

.bannerText {
  color: white;
  margin-top: 10px;
  font-size: 24px;
  text-align: left;
  max-width: 400px;
  line-height: 40px;
}
.bannerButtonContainer {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  margin-top: 40px;
}

.bannerButton1 {
  color: #696969;
  font-size: 16px;
  border: 2px solid #E8E8E8;
  background-color: #E8E8E8;
  padding-top: 15px;
  padding-bottom: 15px;
  padding-right: 30px;
  padding-left: 30px;  border-radius: 40px;
  margin-right: 40px;
}

.bannerButton2 {
  color: #696969;
  font-size: 16px;
  border: 2px solid #FFBD59;
  background-color: #FFBD59;
  padding-top: 15px;
  padding-bottom: 15px;
  padding-right: 30px;
  padding-left: 30px;
  border-radius: 40px;
}

.bannerImage {
  width: 600px;
}

.partnersHeader {
  color: white;
  background-image: linear-gradient(to right, #000000, #3533CD);
  font-family: "BebasNeue";
  font-size: 24px;
  padding-bottom: 20px;
}

.partnersImageContainer {
  background-color: white;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  padding-left: 200px;
  padding-right: 200px;
}

.partnersLogo {
  width: 200px;

}

.content {
  color: white;
  background-image: linear-gradient(to right, #000000, #3533CD);
  padding-top: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.contentHeader {
  font-family: "BebasNeue";
  font-size: 48px;
  padding-bottom: 20px;
  width: 750px;
  line-height: 60px;
}

.contentSubHeader {
  font-size: 18px;
  width: 750px;
  margin-bottom: 70px;
}

.contentTextContainer1 {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  width: 85%;
  font-family: "BlackerSansPro";

}

.contentTextBox {
  display: flex;
  flex-direction: column;
  width: 300px
}

.contentTextHeader {
  font-size: 22px;
  font-weight: bold;
  margin-bottom: 10px;
  text-align: left;
  font-family: "BebasNeue";
  letter-spacing: 1px;
}

.contentText {
  text-align: left;
  line-height: 24px;
  font-size: 18px;
}

.contentImage {
  width: 75%;
  margin-top: 100px;
  margin-bottom: 50px;
}

.contentTextContainer2 {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  width: 85%;
  font-family: "BlackerSansPro";

}

.contentTextRow {
  display: flex;
  flex: row;
  justify-content: space-evenly;
  margin-bottom: 100px;
}

.launchpad {
  height: 800px;
  background-image: url('./assets/Website Assets/launchpadmainbackground.png');
}

.launchpadLogo {
  margin-top: 100px;
}

.launchpadContent {
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.launchpadContentContainer {
  background: #324259;
  padding: 75px;
  opacity: 0.8;
}

.launchpadContentHeader {
  font-size: 28px;
  font-weight: bold;
  padding-bottom: 20px;
}

.launchpadContentText {
  font-size: 18px;
  width: 600px;
  line-height: 30px;
  margin-top: 24px;
}

.accomplishments {
  display: flex;
  flex-direction: column;
  color: white;
  align-items: center;
  background-image: linear-gradient(to right, #000000, #3533CD);

}

.accomplishmentsHeader { 
  font-family: "BebasNeue";
  font-size: 48px;
  margin-top: 100px; 
}

.accomplishmentsSubHeader {
  font-size: 24px;
  margin-top: 20px; 
}

.accomplishmentsContentContainer {
  display: flex;
  flex-direction: column;
  margin-top: 75px;
  width: 80%;
}

.accomplishmentsContentRow {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  margin-bottom: 75px;
}

.accomplishmentsContentBox {
  border: 2px solid #004AAD;
  background-color: #004AAD;
  border-radius: 50px;
  padding: 30px;
}

.accomplishmentsContentText {
  width: 300px;
  margin-bottom: 50px;
  margin-top: 20px;
  font-size: 18px;
}

.accomplishmentsContentLogo {
  width: 150px;
}

.testimonials {
  background-color: white;
  padding-bottom: 100px;
}

.testimonialImage {
  width: 1200px;
}

.callAction {
  background-image: linear-gradient(to right, #000000, #3533CD);
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.CTAContainer {
  margin-top: 100px;
}

.CTAHeader {
  font-family: "Bebasneue";
  font-size: 48px;
}

.CTASubHeader { 
  width: 700px;
  font-size: 18px;
  margin-top: 50px;
}

.CTAText {
  width: 700px;
  font-size: 18px;
  margin-top: 50px;
  font-weight: bold;
}

.CTAButtonImage {
  width: 150px;
}

.teamContainer {
  display: flex;
  background-image: linear-gradient(to right, #000000, #3533CD);
  color: white;
  flex-direction: column;
  align-items: center;
  padding-top: 50px;

}

.teamContentContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.teamHeaderContainer {
  display: flex;
  flex-direction: column;
  text-align: left;

}

.teamHeader {
  font-size: 48px;
  font-weight: bold;
}

.teamHeaderText {
  font-size: 18px;
  color: #1E1357;

}

.teamInformationContainer {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin-top: 100px;
  justify-content: space-around;
}

.teamInformationBox {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 100px;
  width: 33%;
  justify-content: center;

}

.teamImage {
  display: flex;
  flex-direction: row;
}

.profileImage {
  max-width: 125px;
}

.teamContent {
  margin-left: 25px;
  text-align: left;
  font-size: 14px;
}

.memberName {
  font-size: 18px;
  font-weight: bold;
}

.memberRole {
  margin-top: 7px;
}

.twitterlink {
  margin-top: 15px;
  width: 25px;
  cursor: pointer
}

.linkedinLink {
  margin-top: 15px;
  width: 25px;
  cursor: pointer;
}

.footerContainer {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  padding-top: 50px;
  padding-bottom: 20px;
  font-size: 16px;
  background-color: white;
}

.footerLogoContainer {
  width: 17%;
}
.footerLogo {
  width: 100px;
}

.contactusContainer {
  padding-top: 20px;
  width: 22%;
  color: #1E1357;

}

.contactUsHeaderBoldStyle {
  font-size: 24px;
  font-weight: bold ;
  margin-right: 10px;
}

.contactUsText {
  text-align: left;
}

.whitepaperTextStyle {
  text-align: left;
  line-height: 40px;
  cursor: pointer;
  text-decoration-line: underline;
  }

.contactUsHeaderTextStyle{
  font-size: 14px;
}

.contactUsHeader {
  display: flex;
  flex-direction: row;
}

.addressHeaderStyle {
  font-size: 24px;
  font-weight: bold;

}

.addressContainer {
  display: flex;
  flex-direction: column;
  width: 22%;
  color: #1E1357;
  padding-top: 20px;

}

.emailContainer {
  width: 22%;
  color: #1E1357;
  padding-top: 20px;

}

.emailHeader {
  font-size: 24px;
  font-weight: bold;

}

.emailStyle {
  cursor: pointer;
}

.addressTextStyle {
  display: flex;
  flex-direction: column;
  margin-bottom: 5px;
  font-size: 16px;
}

.socialContainer {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  width: 17%;

}

.socialContainerStyle {
  display: flex;
  flex-direction: row;
  
}


.socialIconImage {
  max-width: 30px;
  cursor: pointer;
  padding-top: 30px;
}


